// Global styling for this template

html {background: #333;}

body {
  overflow-x: hidden;
  @include heading-font;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

p {
  line-height: 1.75;
}

a {
  color: $primary;
  &:hover {
    color: darken($primary, 10%);
  }
}

.text-primary {
  color: $primary !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  line-height: 1.33em;
  @include heading-font;
}

.page-section {
  padding: 90px 0 20px 0;
  h2.section-heading {
    font-size: 40px;
    margin-top: 0;
    margin-bottom: 15px;
  }
  h3.section-subheading {
    font-size: 16px;
    font-weight: 400;
    font-style: italic;
    margin-bottom: 50px;
    text-transform: none;
  }
}

.top-brow {
  border-top: 2px solid #000;
  padding-top: .85em;
  margin-top: .85em;
}
.top-brow.yellow {border-color: #F9C819;}

@media (min-width: 768px) {
  section {
    padding: 150px 0 20px 0;
  }
  .page-section {
    padding: 110px 0 30px 0;
  }
}

// Highlight color customization
::-moz-selection {
  background: $primary;
  text-shadow: none;
}

::selection {
  background: $primary;
  text-shadow: none;
}

img::selection {
  background: transparent;
}

img::-moz-selection {
  background: transparent;
}
